@import '../styles/_variables.scss';

.items__wrapper {
	position: relative;
	padding-top: 16px;
	padding-left: 26px;
	margin-left: 12px;

	&:not(:last-child) {
		border-left: 1px solid $grey_1;
	}

	&:last-child:not(:first-child) {
		border-left: none;

		&::before {
			position: absolute;
			content: "";
			top: 0;
			left: 0;
			width: 0;
			height: 35px;
			border-left: 1px solid $grey_1;
		}
	}

}

.items {
	padding-left: 12px;
}