body {
	font: $font-base;
	min-width: $min-container;
	min-height: 100vh;
	background-color: $grey_3;
}

.wrapper {
	margin-top: 20px;
	margin-left: 16px;

	@include media-md {
		margin-top: 40px;
		margin-left: 32px;
	}

	@include media-lg {
		margin-top: 60px;
		margin-left: 48px;
	}
}