@import './functions';

//*************COLORS **********************/

$black: #000;
$white: #fff;
$dark_1: #2B455C;
$dark_2: #2E465C;
$blue: #9CD0FF;
$lightblue: #F1F9FF;
$grey_1: #999999;
$grey_2: #C4C4C4;
$grey_3: #F5F5F8;

//**************MEDIA QUERIES ****************/

$mq-lg: 1920px;
$mq-md: 1024px;
$mq-sm: 768px;

$min-container: 375px;

//**************FONTS****************/
$font-text:'Roboto',
sans-serif;

//**FONT-SIZE FOR REM CALCULATION */
$html-font-size: 16px;

//****************FONT-VARIABLES**********/

$font-base: 400 rem(16) $font-text;
$font-title: 600 rem(18) $font-text;
$font-subTitle: 300 rem(14) $font-text;
$font-date: 500 rem(14) $font-text;
$font-light: 300 rem(14) $font-text;
$font-ceil: 300 rem(12) $font-text;
$font-colHead: 400 rem(12) $font-text;

//**********border-radius
$border-radius-default: 2px;