@import '../styles/_variables.scss';

.titleBtn {
	position: relative;
	width: 67px;
	height: 36px;
	border-radius: $border-radius-default;
	background-color: $dark_2;
	color: $white;
	font: $font-base;
	text-align: center;
	line-height: 36px;
}

.titleBtn__secondary {
	&::before {
		position: absolute;
		content: '';
		top: 50%;
		left: -26px;
		width: 26px;
		border-top: 1px solid $grey_1;
	}
}