@import '../styles/_variables.scss';

.table {
	user-select: none;
	min-width: 150px;
	max-width: 297px;
	width: auto;
	padding: 0;
	border: 1px solid $dark_2;
	border-radius: $border-radius-default;
	background-color: $lightblue;
	color: $dark_2;
	height: max-content;
}

.table__body {
	border-collapse: collapse;
	width: 100%;
}

.table__caption {
	display: flex;
	align-items: center;
	justify-content: space-between;
	min-height: 66px;
	padding: 12px;
	cursor: pointer;
}

.table__wrapper {
	height: 0;
	overflow: hidden;
	transition: height 0.2s linear;
}

.table__title {
	display: block;
	font: $font-title;
}

.table__subTitle {
	display: block;
	margin-top: 6px;
	font: $font-subTitle;
}

.table__date {
	margin-left: 0.5em;
	font: $font-date;
	text-align: right;
}

.table__head {
	border-top: 1px solid $dark_2;
	border-bottom: 1px solid $dark_2;
	text-transform: capitalize;
	box-sizing: content-box;
}

.table__colHead {
	position: relative;
	font: $font-colHead;
	padding: 1px;
	cursor: pointer;

	&:nth-child(1) {
		min-width: 34px;
		width: 34px;
	}

	&:nth-child(2) {
		padding-left: 3px;
		text-align: left;
		border-right: 1px solid $dark_2;
		border-left: 1px solid $dark_2;
	}

	&:first-child,
	&:last-child {
		&::before {
			position: absolute;
			top: 50%;
			right: 4px;
			transform: translateY(-50%);
			content: "";
			width: 0;
			height: 0;
			border-left: 3px solid transparent;
			border-right: 3px solid transparent;
			border-top: 3px solid $dark_2;
		}
	}

	&:last-child {
		padding-right: 6px;
		width: 66px;
		min-width: 66px;
	}
}

.table__row {
	&:not(:last-child) {
		border-bottom: 1px solid $blue;
	}
}

.table__ceil {
	padding: 1px 3px;
	font: $font-ceil;
	text-align: center;

	&:nth-child(1) {
		min-width: 34px;
		width: 34px;
	}

	&:nth-child(2) {
		width: auto;
		padding-left: 3px;
		border-right: 1px solid $blue;
		border-left: 1px solid $blue;
		text-align: left;
	}

	&:nth-child(3) {
		width: 66px;
		font: $font-colHead;
	}
}