@import '../styles/_variables.scss';
@import '../styles/_mixins.scss';

.group__wrapper {
	position: relative;
	padding-top: 16px;
	padding-left: 26px;
	padding-right: 16px;
	border-left: 1px solid $grey_1;

	&:last-child {
		border-left: none;

		&::before {
			position: absolute;
			content: "";
			top: 0;
			left: 0;
			width: 0;
			height: 35px;
			border-left: 1px solid $grey_1;
		}
	}
}

.group {
	position: relative;
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	justify-content: flex-start;
	margin-left: 13px;
	padding-top: 16px;
	padding-left: 26px;
	border-left: 1px solid $grey_1;
}

.group__item {
	display: flex;
	flex-direction: column;
	gap: 16px;
	max-width: 297px;
	flex: 1 0 auto;

	&:not(:first-child) {
		margin-left: 30px;
	}

	@include media-md {
		width: 297px;
	}
}